import React, { Suspense } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { Footer } from "../components/footer";
import { LoaderFallback } from "../components/loader-fallback";
import { Menu } from "../components/menu";
import homeStyle from "./home/home.scss";

const Error404 = React.lazy(() => import("./error404/error404.js"));
const Home = React.lazy(() => import("./home/home.js"));
const Imprint = React.lazy(() => import("./imprint/imprint.js"));

const splitCode = Component => () => (
  <Suspense fallback={<LoaderFallback />}>
    <Component homeStyle={homeStyle} />
  </Suspense>
);

const App = () => {
  return (
    <Router>
      <div>
        <Menu />
        <Switch>
          <Route path="/" exact component={splitCode(Home)} />
          <Route path="/imprint" component={splitCode(Imprint)} />
          <Route component={splitCode(Error404)} />
        </Switch>
        <Footer />
      </div>
    </Router>
  );
};
App.displayName = "App";

export default App;
