import React from "react";
import PropTypes from "prop-types";
import cn from "classnames";
import style from "./loader-fallback.scss";
import { Section } from "../section";

const LoaderFallback = ({ className }) => (
  <div className={cn(style.root, className)}>
    <Section>
      <h2>Eine kurze Unterbrechung!</h2>
      <p>
        Wer am Anfang nicht lang warten will, muss leider mit kurzen Nachladeunterbrechungen rechnen. Dies ist eine
        ebensolche. Im Normalfall sollte das nicht lange dauern. Man sieht das hier nur kurz. Oder bei einer gaaaaanz
        langsamen Internetverbindung.
      </p>
      <p>
        Ist (bei Surfen über das Mobilfunknetz) vielleicht das Datenvolumen für diesen Monat aufgebraucht? Oder ist das
        WLAN wieder sehr schlecht? Teilst Du Dir einen Anschluss mit Studenten, die rund um die Uhr Filme aus dem
        Internet schauen?
      </p>
      <p>
        Wie dem auch sei, es sollte eigentlich schnell vorbei sein. Falls es wider Erwarten doch zu einer so langen
        Verzögerung kommt, könnte ein Blick in die Browserkonsole helfen. Vielleicht haben wir auch irgendwas kaputt
        gemacht. Denn eigentlich sollte diese Nachricht hier nicht allzu lange stehen, sofern die nächste nachgeladen
        werden kann...!
      </p>
    </Section>
  </div>
);
LoaderFallback.displayName = "LoaderFallback";
LoaderFallback.propTypes = {
  className: PropTypes.string
};
LoaderFallback.defaultProps = {
  className: null
};

export default LoaderFallback;
