import React, { useState } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import cn from "classnames";
import style from "./footer.scss";

function getRandomText() {
  const adjs = [
    "a few",
    "cool",
    "creative",
    "funny",
    "great",
    "helpful",
    "interesting",
    "loving",
    "multiple",
    "nice",
    "real",
    "total",
    "understanding"
  ];
  const subs = [
    "coders",
    "developers",
    "digital nomads",
    "geeks",
    "humans",
    "kids",
    "learners",
    "monkeys",
    "nerds",
    "optimists",
    "programmers"
  ];
  const locations = ["Bavaria", "Germany", "Europe", "Landshut", "Passau", "around the world"];
  return `· created by ${randomOf(adjs)} ${randomOf(subs)} from ${randomOf(locations)} ·`;

  function randomOf(arr) {
    return arr[Math.floor(Math.random() * arr.length)];
  }
}

const Footer = ({ className }) => {
  const [randomText, setRandomText] = useState(getRandomText());

  return (
    <div className={cn(style.root, className)}>
      <div>
        -· © 2018 compose.us GmbH · Kontaktiert uns via <a href="mailto:hello@compose.us">hello@compose.us</a> ·{" "}
        <Link to="/imprint">Impressum &amp; Credits</Link> ·-
      </div>
      <div id="random-footer" onPointerEnter={() => setRandomText(getRandomText())}>
        {randomText}
      </div>
    </div>
  );
};
Footer.displayName = "Footer";
Footer.propTypes = {
  className: PropTypes.string
};
Footer.defaultProps = {
  className: null
};

export default Footer;
